.mic-icon-animation {
    -webkit-animation: pulse 3s infinite;
            animation: pulse 3s infinite;
}

.mic-icon-animation svg {
    font-size: 5rem;
}

@-webkit-keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}

@keyframes pulse {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}
